.event-schedule {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.events-schedule-name {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.schedule-heading-titlebox{
  display: flex;
  background-color: rgba(80, 80, 80, 0.5);
  width: 50rem;
  height: 5rem;
  border-radius: 20px;
  justify-content: space-between;
  color: white;
  text-align: center;
}
.date-time {
  background-color: rgba(196, 196, 196, 0.3);
  border-radius: 20px;
  display: flex;
  padding: 0 1rem;
  align-items: center;
  width: 9rem;
  justify-content: center;
}
.schedule-heading-m{
  display: flex;
  font-size: 2.2rem;
  width: 9rem;
  background: rgba(196, 196, 196, 0.3);
  border-radius: 20px;
  align-items: center;
  justify-content: center;
}
.more-options-m{
  display: flex;
  width: 9rem;
  background: rgba(196, 196, 196, 0.3);
  border-radius: 20px;
  align-items: center;
  justify-content: center;
  font-size: 2.5rem;
}
.schedule-1,
.schedule-2 {
  width: 98vw;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding-top: 2rem;
}
 
.schedule-wrapper-1 .list,
.schedule-wrapper-2 .list {
  display: none;
}
 
.schedule-wrapper-1-active .list,
.schedule-wrapper-2-active .list {
  display: flex;
  flex-direction: column;
  margin-bottom: 5rem;
  align-items: center;
  color: white;
  background-color: rgba(80, 80, 80, 0.5);
  border-radius: 20px;
  font-size: 2rem;
  width: 50rem;
}
 
.schedule-wrapper-1-active .list-item,
.schedule-wrapper-2-active .list-item {
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
}
 
.schedule-wrapper-1-active hr,
.schedule-wrapper-2-active hr {
 width: 90%;
 height: 0px;
 background: white;
 border: 1px solid white;
}
 
.schedule-wrapper-1-active .schedule-heading-titlebox ,
.schedule-wrapper-2-active .schedule-heading-titlebox {
  margin-bottom: 2rem;

}
 
.schedule-wrapper-1-active .more-option-show,
.schedule-wrapper-2-active .more-option-show {
  transform: rotate(180deg);
  cursor: pointer;
  font-weight: bold;
}

.schedule-wrapper-1 .more-option-show,
.schedule-wrapper-2 .more-option-show{
  cursor: pointer;
  font-weight: bold;
}
@keyframes spin {
  0% {
    transform: rotateZ(0);
  }
  100% {
    transform: rotateZ(360deg);
  }
}
.circle1{
  border-radius: 50%;
}
.circle1 :nth-child(2){
  width: 16%;
  position: absolute;
  margin: 0;
  left: 80%;
  top: 533%;
  opacity: 0.6;
  transform: rotate(49.38deg);
  animation: spin 10s linear infinite;
}
.circle1 :nth-child(1){
  position: absolute;
  margin: 0;
  left: -1%;
  top: 475%;
  opacity: 0.6;
  transform: rotate(49.38deg);
  animation: spin 10s linear infinite;
}
  
  .circle2{
  position: absolute;
  margin: 0;
  right: 1%;
  bottom: 0%;
  opacity: 0.6;
  }
/* media query */  
@media screen and (max-width: 1024px){
  .schedule-wrapper-1-active .list,
  .schedule-wrapper-2-active .list{
    width: 44rem;
  }
  .schedule-wrapper-1-active .schedule-heading-titlebox, .schedule-wrapper-2-active .schedule-heading-titlebox {
    width: 44rem;
  }
  .schedule-heading-titlebox {
    width: 44rem;
  }
  .schedule-wrapper-1-active .list-item, .schedule-wrapper-2-active .list-item {
    font-size: 1.5rem;
  }
}

@media screen and (max-width: 768px){
  .events-schedule-name {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
  .schedule-wrapper-1 .schedule-heading-titlebox ,
  .schedule-wrapper-2 .schedule-heading-titlebox { 
    font-size: 1rem;
    width: 90vw;
    height: 3.5rem;
  }
  .schedule-heading-m{
    font-size: 1.5rem;
    width: 7rem;
  }
  .date-time {
    font-size: 1rem;
    width: 7rem;
  }
  .more-options-m{
    font-size: 2rem;
    width: 7rem;
  }
  .schedule-1,
  .schedule-2 {
    width: 25vw;
  }
  .schedule-wrapper-1-active .list,
  .schedule-wrapper-2-active .list {
    width: 90vw;
    display: flex;
    flex-direction: column;
  }
  .schedule-wrapper-1-active .list-item,
  .schedule-wrapper-2-active .list-item {
    padding-top: 0.5rem;
    font-size: 0.8rem;
    padding-bottom: 0.5rem;
  }
  .schedule-wrapper-1-active .schedule-heading-titlebox ,
  .schedule-wrapper-2-active .schedule-heading-titlebox {
    margin-bottom: 1rem;
  }
  .schedule-wrapper-1-active .more-option-show,
  .schedule-wrapper-2-active .more-option-show {
    transform: rotate(180deg);
    cursor: pointer;
    font-weight: bold;
  }
  .schedule-wrapper-1 .more-option-show,
  .schedule-wrapper-2 .more-option-show{
    cursor: pointer;
    font-weight: bold;
  }
  .circle1{
    display: none;
  }
  .circle2{
    display: none;
  }
}
@media screen and (max-width: 500px){
  .date-time{
    font-size: 0.8rem;
    width: 4rem;
  }
  .schedule-heading-m{
    font-size: 1rem;
    width: 4rem;
  }
  .more-options-m{
    font-size: 1rem;
    width: 4rem;
  }
}